.input-with-label {
  display: flex;
  width: 240px;
}

.input-with-label__label {
  padding-left: 10px;
  border: 1px solid #928F94;
  border-right: none;
  border-radius: 5px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  white-space: nowrap;
  height: 50px;
  display: flex;
  align-items: center;
}

.input-with-label__input {
  width: 100%;
  height: 50px;
  padding-left: 5px;
  border: 1px solid #928F94;
  border-left: none;
  border-radius: 5px;


  &_no-border {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }

  &:active,
  &:focus {
    outline: none;
  }
}